import styled from "styled-components";
import React from "react";
import {useTags} from "Hooks/useTags";
import Icon from "components/Icon";

const Wrap = styled.section`
  display: flex;
  flex-direction: column;
  //justify-content: flex-end;
  overflow: auto;
  align-items: flex-start;
  flex-grow: 1;
  background: #ffffff;
  padding: 12px 16px 0 16px;
  -ms-overflow-style: none;
  scrollbar-width: none;
  &::-webkit-scrollbar {display: none;}
  
  > header {
    > span {
      display: flex;
      align-items: center;
      margin-bottom: 8px;
      > .icon {
        width: 18px;
        height: 18px;
        fill: #f9a01f;
      }
      > span {
        margin-left: 6px;
        font-size: 14px;
        color: #a4a6a6;
      }
    }
  }

  > ol {
    margin: 0 -12px;

    > li {
      border-radius: 18px;
      display: inline-block;
      padding: 3px 18px;
      font-size: 14px;
      margin: 8px 0 8px 12px;
      background: #e1e1e1;
      color: #1a1a1a;
      &.selected {
        background: #f9a01f;
        color: white;
      }
    }
  }
  > footer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    margin-top: 8px;
    > span {
      padding-bottom: 10px;
      > .icon {
        width: 18px;
        height: 18px;
        fill: #f9a01f;
      }
    }
    > button {
      display: block;
      width: 100%;
      margin-left: 10px;
      padding-bottom: 10px;
      background: none;
      border: none;
      border-bottom: 1px solid rgba(126,126,126,0.1);
      text-align: left;
      font-size: 14px;
      color: #a4a6a6;
    }
  }
`

type Props = {
  selected: number[],
  onToggle: (selected: number[]) => void
}

const TagsSection: React.FC<Props> = (props) => {

  const {tags, addTag} = useTags()
  const selectedTagIds = props.selected

  const toggleTag = (tagId: number) => {
    const index = selectedTagIds.indexOf(tagId)
    if (index >= 0) {
      props.onToggle(selectedTagIds.filter(t => t !== tagId))
    } else {
      props.onToggle([...selectedTagIds, tagId])
    }
  }

  return (
    <Wrap>
      <header>
        <span>
          <Icon name={"labels"}/>
          <span className={"text"}>选择这笔记账的标签：</span>
        </span>
      </header>
      <ol>
        {
          tags.map(tag =>
            <li
              key={tag.id}
              onClick={() => toggleTag(tag.id)}
              className={selectedTagIds.indexOf(tag.id) >= 0 ? 'selected' : ''}
            >
              {tag.name}
            </li>
          )
        }
      </ol>
      <footer>
        <span>
          <Icon name={"plus"}/>
        </span>
        <button onClick={addTag}>添加一个标签</button>
      </footer>
    </Wrap>
  )
}

export {TagsSection}