import React, {useEffect, useRef, useState} from "react";
import {useTags} from "Hooks/useTags";
import {useParams, useHistory} from "react-router-dom"
import Layout from "components/Layout";
import Icon from "components/Icon";
import {ReuseBtn} from "components/ReuseBtn";
import styled from "styled-components";
import {ReuseInput} from "components/ReuseInput";
import {CenterWrap} from "components/CenterWrap";
import {Gap16px} from "components/Gap16px";

const Topbar = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 20px;
  margin-bottom: 32px;
  padding: 14px;
  background: white;
  box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
`

const InputWrap = styled.div`
  background: white;
  padding: 0 16px;
  margin-top: 8px;
  > div {
    > label {
      display: flex;
      align-items: center;
      > input {
        //height: auto;
        border-bottom: 1px solid rgba(126,126,126,0.1);
      }
    }
  }
`

const RemoveBtn = styled(ReuseBtn)`
  background: none;
  border: 1px solid #f9a01f;
  color: #1a1a1a;
`

type Params = {
  id: string
}

const TagEdit: React.FC = () => {

  const {findTag, updateTag, removeTag} = useTags()

  let {id: idString} = useParams<Params>()

  const tag = findTag(Number.parseInt(idString))

  let [oldTagName, setOldTagName] = useState('')

  // 利用 count 计数，避免 tag 还是 undefined 时就赋值给 oldTagName
  const count = useRef(0)

  useEffect(() => {
    count.current += 1
  })

  // 组件已经渲染过2次，且 tag 已经获取成功再 setOldTagName
  useEffect(() => {
    if (count.current < 3 && tag) {
      setOldTagName(tag.name)
    }
  }, [tag])

  const saveTagName = () => {
    updateTag(tag.id, {name: tag.name})
    history.goBack()
  }

  const history = useHistory()
  const onClickBack = () => {
    if (tag) {
      setOldTagName(tag.name)
      updateTag(tag.id, {name: oldTagName})
    }
    history.goBack()
  }

  return (
    <Layout>
      <Topbar>
        <Icon name={'left'} onClick={onClickBack}/>
        <span>编辑标签</span>
        <Icon/>
      </Topbar>
      { tag ?
        // 该标签存在再渲染
        <>
          <InputWrap>
            <div>
              <ReuseInput
                headText={"标签名"}
                type={"text"}
                placeholder={"在这里输入标签名"}
                value={tag.name}
                onChange={(e) => {
                  updateTag(tag.id, {name: e.target.value})
                }}
              />
            </div>
          </InputWrap>
          <CenterWrap>
            <Gap16px/>
            <Gap16px/>
            <Gap16px/>
            <ReuseBtn onClick={saveTagName}>保存</ReuseBtn>
            <Gap16px/>
            <RemoveBtn onClick={() => {removeTag(tag.id)}}>删除</RemoveBtn>
          </CenterWrap>
        </>
      :
        // 该标签不存在，渲染提示
        <CenterWrap><div>删除成功</div></CenterWrap>
      }
    </Layout>
  )
}

export {TagEdit}