import Layout from "components/Layout";
import React from "react";
import {
  Link,
} from "react-router-dom";
import {useTags} from "Hooks/useTags";
import styled from "styled-components";
import Icon from "components/Icon";
import {CenterWrap} from "components/CenterWrap";
import {Gap16px} from "components/Gap16px";

const TagsWrap = styled.div`
  display: flex;
  flex-direction: column;
  > header {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 20px;
    padding: 14px;
    background: white;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
    position: absolute;
    width: 100%;
    left: 0; top: 0;
  }
  > .tags-list {
    padding-top: 64px;
    overflow: auto;
    font-size: 16px;
    background: white;
    > li {
      border-bottom: 1px solid rgba(126,126,126,0.1);
      line-height: 20px;
      margin: 0 16px;
      > a {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 0;
      }
    }
  }
`

const CenterWrapLocal = styled(CenterWrap)`
  margin-bottom: 40px;
`

const Btn = styled.button`
  font-size: 18px;
  border: none;
  padding: 12px 40px;
  background: #f9a01f;
  background: linear-gradient(90deg, #f9a01f 0%, #ffb242 100%);
  border-radius: 24px;
  color: white;
`

function Tags() {

  const {tags, addTag} = useTags()

  return (
    <Layout>
      <TagsWrap>
        <header>
          <span>编辑标签</span>
        </header>
        <ol className={"tags-list"}>
          {
            tags.map(tag =>
              <li
                key={tag.id}
              >
                <Link to={'/tags/' + tag.id}>
                  <span className={"nowrap-fix"}>{tag.name}</span>
                  <Icon name={"right"}/>
                </Link>
              </li>
            )
          }
        </ol>
        <CenterWrapLocal>
          <Gap16px/>
          <Gap16px/>
          <Gap16px/>
          <Btn onClick={addTag}>创建标签</Btn>
        </CenterWrapLocal>
      </TagsWrap>
    </Layout>
  );
}

export default Tags