import {useEffect, useRef} from "react";


const useUpdate = (fn: () => void, dependency: any[]) => {

  // tags 从 undefined 变成 [] 时触发useEffect，也会被写入，因此加一个条件判断
  const count = useRef(0)
  useEffect(() => {
    count.current += 1
  })

  useEffect(() => {
    if (count.current > 1) {
      fn()
    }
  }, [fn, dependency])
}

export {useUpdate}