import bg_1 from "img/bg_1.png"
import bg_no_records from "img/bg_no_records.min.png"
import Layout from "components/Layout";
import React, {useState} from "react";
import {CatSection} from "./Money/CatSection";
import styled from "styled-components";
import {RecordItem, useRecords} from "Hooks/useRecords";
import {useTags} from "Hooks/useTags";
import day from "dayjs";
require('dayjs/locale/zh-cn')

const StatsWrap = styled.div`
  // style
`

const FunctionArea = styled.header`
  width: 100%;
  position: absolute;
  left: 0; top: 0;
`

const HeaderWrap = styled.header`
  padding: 0 16px 60px 16px;
  background: #f9a01f;
  background: #f9a01f url(${bg_1}) right bottom/120px 175px  no-repeat;
  color: white;
  > header {
    padding-top: 16px;
    margin-bottom: 16px;
    text-align: center;
    font-size: 22px;
  }
  .sums {
    margin-top: 16px;
    > header {
      margin-bottom: 8px;
      font-size: 14px;
    }
    > main {
      margin-left: -6px;
      font-size: 28px;
    }
  }
`

const CatWrap = styled.nav`
  margin-top: -16px;
  margin-bottom: -30px;
  padding: 0 16px 0 16px;
  > section {
    transform: translateY(-20px);
    border-radius: 10px;
    box-shadow: 0 1px 5px 1px rgba(0,0,0,0.1);
    > ul {
      > li {
        color: #a4a6a6;
        font-size: 20px;
        &.selected {
          font-size: 24px;
          color: #1a1a1a;
        }
        &.selected:after {
          width: 26px;
          height: 5px;
          left: 50%;
          transform: translateX(-50%);
          bottom: 4px;
          background: #f9a01f;
          border-radius: 30px;
        }
      }
    }
  }
`

const RecordGroupWrap = styled.main`
  padding-top: 230px;
  .record-group {
    box-shadow: 0 -1px 8px rgba(0,0,0,0.1);
    margin-bottom: 16px;
  }
  .no-records {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    color: #a4a6a6;
    .bg {
      width: 100%;
      height: 155px;
      background: url(${bg_no_records}) center center/150px 135px no-repeat;
    }
  }
`

const RecordGroupHeader = styled.h3`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 18px;
  line-height: 20px;
  padding: 10px 16px;
  color: inherit;
  > .day {
    margin-left: 12px;
    margin-right: auto;
    font-size: 14px;
    color: #a4a6a6;
  }
  > .sub-sums {
    font-size: 14px;
    > span {
      color: #1ea6fd;
    }
  }
`

const Item = styled.div`
  display: flex;
  justify-content: space-between;
  background: white;
  line-height: 20px;
  padding: 10px 0;
  margin-left: 16px;
  margin-right: 16px;
  font-size: 18px;
  border-bottom: 1px solid rgba(126,126,126,0.1);;
  &:last-child {
    border: none;
  }
  > .notes {
    margin-right: auto;
    margin-left: 16px;
    font-size: 14px;
    color: #a4a6a6;
  }
`

function Stats() {

  const [cat, setCat] = useState<'-'|'+'>('-')

  const {records} = useRecords()

  const {getTagName} = useTags()

  const selectedRecords = records.filter(r => r.cat === cat)

  const hash: {[key: string]: RecordItem[]} = {}

  selectedRecords.forEach(r => {
    const key = day(r.recordedAt).format('YYYY-MM-DD')
    if (!(key in hash)) {
      hash[key] = []
    }
    hash[key].push(r)
  })

  const arr = Object.entries(hash).sort((a, b) => {
    if (a[0] > b[0]) return -1
    if (a[0] < b[0]) return 1
    if (a[0] === b[0]) return 0
    return 0
  })

  const beautifyDate = (d: string) => {
    if (day(d).isSame(new Date(), 'day')) {
      return '今天'
    } else if (day(d).isSame(day(new Date()).subtract(1, 'day'), 'day')) {
      return '昨天'
    } else {
      return day(d).isSame(new Date(), 'year') ? day(d).format('M月D日') : day(d).format('YYYY年M月D日')
    }
  }

  return (
    <Layout>
      <StatsWrap>
        <FunctionArea>
          <HeaderWrap>
            <header>我 的 记 录</header>
            <main className={"sums"}>
              <header>合 计 {cat === '-' ? '支 出' : '收 入'}</header>
              <main>￥
                {
                  selectedRecords.reduce((acc, cur) => {
                    return acc + cur.amount
                  }, 0)
                }
              </main>
            </main>
          </HeaderWrap>
          <CatWrap>
            <CatSection
              cat={cat}
              onToggle={
                (cat) => setCat(cat)
              }
            />
          </CatWrap>
        </FunctionArea>
        <RecordGroupWrap>
        {arr.length > 0 ? arr.map(([date, records_]) => {
          return (
            <div className={"record-group"} key={date}>
              <RecordGroupHeader>
                {beautifyDate(date)}
                <span className={"day"}>
                  {day(date).locale('zh-cn').format('dddd')}
                </span>
                <span className={"sub-sums"}>
                  {cat === '-' ? '支' : '收'}：
                  <span>
                    {records_.reduce((acc, cur) => {
                      return acc + cur.amount
                    }, 0)}
                  </span>
                </span>
              </RecordGroupHeader>
              <div>
                {records_.map(r => {
                  return (
                    <Item key={r.recordedAt}>
                      <div className="tags">
                        <span>{r.tagIds.length === 0 ? '无标签' : r.tagIds.map(tagId => getTagName(tagId)).join('，')}</span>
                      </div>
                      <div className="notes">
                        {r.note}
                      </div>
                      <div className="amount">
                        {'￥' + r.amount.toString()}
                      </div>
                    </Item>
                  )
                })}
              </div>
            </div>
          )
        }) :
          <div className={"no-records"}>
            <div className={"bg"}></div>
            <span>还没有记录，去记一笔吧</span>
          </div>
        }
        </RecordGroupWrap>
      </StatsWrap>
    </Layout>
  );
}

export default Stats