import styled from "styled-components";
import React, {ChangeEventHandler} from "react";
import {ReuseInput} from "components/ReuseInput";
import Icon from "components/Icon";

const Wrap = styled.section`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 16px 14px 16px;
  font-size: 14px;
  
  > span {
    .icon {
      width: 18px;
      height: 18px;
      fill: #f9a01f;
    }
  }
  
  > label {
    margin-left: -6px;
    > input {
      border-bottom: 1px solid rgba(126,126,126,0.1);
    }
  }
`

type Props = {
  text: string,
  onChange: (note: string) => void
}

const NotesSection: React.FC<Props> = (props) => {

  const note = props.text

  const onChange: ChangeEventHandler<HTMLInputElement> = (e) => {
      props.onChange(e.target.value)
  }

  return (
    <Wrap>
      <span>
        <Icon name={"write"}/>
      </span>
      <ReuseInput
        type={"text"}
        placeholder={"在这里添加备注"}
        maxLength={20}
        value={note}
        onChange={onChange}
      />
        {/*<span>备注</span>*/}
        {/*<input*/}
        {/*  type="text"*/}
        {/*  placeholder="在这里添加备注"*/}
        {/*  ref = {inputRef}*/}
        {/*  defaultValue={note}*/}
        {/*  onBlur={onBlur}*/}
        {/*/>*/}
    </Wrap>
  )
}

export {NotesSection}