import styled from "styled-components";
import {
  NavLink
} from "react-router-dom";
import React from "react";
import Icon from "./Icon";

const NavWrap = styled.nav`
  z-index: 9999;
  line-height: 24px;
  background: #fff;
  box-shadow: 0 0 1px rgba(0,0,0,0.25),0 0 25px 0 rgba(184,184,184,0.25);
  > ul {
    display: flex;
    justify-content: center;
    > li {
      //width: 33.33%;
      width: 100px;
      text-align: center;
      a {
        padding: 4px 0 4px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        .icon {
          width: 24px;
          height: 24px;
          fill: #a4a6a6;
        }
        &.selected {
          color: #f9a01f;
          .icon {
            fill: #f9a01f;
          }
        }
      }
      &.li-middle {
        background: transparent;
        padding: 0;
        transform: translateY(-16px);
        a {
          width: 56px;
          height: 56px;
          margin: 0 auto;
          border: 2px solid white;
          border-radius: 50%;
          background: #a4a6a6;
          .icon {
            margin-top: 4px;
            width: 80%;
            height: 80%;
            fill: white
          }
          &.selected {
            background: #f9a01f;
            color: white;
            .icon {
              fill: white;
            }
          }
        }
      }
    }
  }
`

const Nav = () => {
  return (
    <NavWrap>
      <ul>
        <li>
          <NavLink exact replace to="/tags" activeClassName="selected">
            <Icon name="labels" />
            标签
          </NavLink>
        </li>
        <li className={"li-middle"}>
          <NavLink replace to="/money" activeClassName="selected">
            <Icon name="money" />
            {/*记账*/}
          </NavLink>
        </li>
        <li>
          <NavLink replace to="/stats" activeClassName="selected">
            <Icon name="stats" />
            统计
          </NavLink>
        </li>
      </ul>
    </NavWrap>
  )
}

export default Nav