import styled from "styled-components";
import React, {useState} from "react";

const Wrap = styled.section`
  font-size: 24px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 1px 5px 1px rgba(0,0,0,0.1);
  > ul {
    display: flex;
    background: white;
    border-radius: 10px;
    > li {
      width: 50%;
      text-align: center;
      padding: 16px 0;
      position: relative;
      &.selected:after {
        content: '';
        display: block;
        position: absolute;
        width: 26px;
        height: 5px;
        left: 50%;
        bottom: 4px;
        transform: translateX(-50%);
        border-radius: 30px;
        background: #f9a01f;
      }
    }
  }
`

type Props = {
  cat: '-' | '+',
  onToggle: (cat: '-' | '+') => void
}

const CatSection: React.FC<Props> = (props) => {

  const catMap = {'-': '支出', '+': '收入'}
  type CatKeys = keyof typeof catMap
  const [catList] = useState<CatKeys[]>(['-', '+'])
  // const [cat, setCat] = useState('-')
  const cat = props.cat

  return (
    <Wrap>
      <ul>
        {catList.map(c =>
          <li
            key={c}
            className={cat === c ? 'selected' : ''}
            onClick={() => {props.onToggle(c)}}
          >
            {catMap[c]}
          </li>
        )}
      </ul>
    </Wrap>
  )
}

export {CatSection}