import styled from "styled-components";
import React from "react";

const Label = styled.label`
  display: flex;
  align-items: center;
  width: 100%;

  > span {
    margin-right: 16px;
    white-space: nowrap;
  }

  > input {
    display: block;
    width: 100%;
    height: 44px;
    border: none;
    background: none;
  }
`

type Props = {
  headText?: string
} & React.InputHTMLAttributes<HTMLInputElement>

const ReuseInput: React.FC<Props> = (props) => {

  const {headText, children, ...rest} = props

  return (
    <Label>
      <span>{headText}</span>
      <input {...rest}/>
    </Label>
  )
}

export {ReuseInput}