import logo from "img/logo_min.png"
import Layout from "components/Layout";
import React, {useState} from "react";
import styled from "styled-components";
import {TagsSection} from "./Money/TagsSection";
import {NotesSection} from "./Money/NotesSection";
import {CatSection} from "./Money/CatSection";
import {NumberPadSection} from "./Money/NumberPadSection";
import {useRecords} from "Hooks/useRecords";

const MyLayout = styled(Layout)`
  display: flex;
  flex-direction: column;
  > header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    background: #f9a01f;
    box-shadow: 0 0 2px 1px rgba(0,0,0,0.1);
    color: white;
    z-index: 9998;
    .logo {
      width: 50px;
      height: 40px;
      background: url(${logo}) left top/50px 40px no-repeat;
    }
    > h2 {
      margin-left: 16px;
    }
  }
`

const CatWrap = styled.div`
  background: white;
  padding: 0 16px;
  > section {
    transform: translateY(10px);
  }
`

type Category = '-' | '+'

const defaultRecord = {
  tagIds: [] as number[],
  note: '',
  cat: '-' as Category,
  amount: 0
}

function Money() {

  const [value, setValue] = useState(defaultRecord)

  const onChange = (obj: Partial<typeof value>) => {
    setValue({
      ...value,
      ...obj
    })
  }

  const {addRecord} = useRecords()

  const submitOutput = () => {
    if(addRecord(value)) {
      setValue(defaultRecord)
      window.alert('保存成功')
    } else {
      window.alert('保存失败')
    }
  }

  return (
    <MyLayout scrollTop={9999}>
      {/*测试用*/}
      {/*{JSON.stringify(value)}*/}
      <header>
        <aside className={"logo"}></aside>
        <h2>记一笔吧！</h2>
      </header>
      <TagsSection
        selected={value.tagIds}
        onToggle={(tagIds) => onChange({tagIds})}
      />
      <NotesSection
        text={value.note}
        onChange={
          (note) => onChange({note})
        }
      />
      <CatWrap>
        <CatSection
          cat={value.cat}
          onToggle={
            (cat) => onChange({cat})
          }
        />
      </CatWrap>
      <NumberPadSection
        output={value.amount}
        onChange={
          (amount) => onChange({amount})
        }
        onSubmit={
          submitOutput
        }
      />
    </MyLayout>
  );
}

export default Money