import Layout from "components/Layout";
import {
  Link
} from "react-router-dom";
import React from "react";
import styled from "styled-components";
import {ReuseBtn} from "components/ReuseBtn";

const PageWrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  > h2 {
    margin-top: 160px;
    margin-bottom: 100px;
    color: #1a1a1a;
  }
`

function NoMatchPage() {
  return (
    <Layout>
      <PageWrap>
        <h2>当前页面不存在</h2>
        <Link to="/money">
          <ReuseBtn>回到首页</ReuseBtn>
        </Link>
      </PageWrap>
    </Layout>
  )
}

export {NoMatchPage}