import {useEffect, useState} from "react";
import {generateId} from "lib/generateId";
// import userEvent from "@testing-library/user-event";
import {useUpdate} from "./useUpdate";

const useTags = () => {

  const [tags, setTags] = useState<{id: number, name: string}[]>([])

  // componentDidMount
  useEffect(() => {
    let tags_ = JSON.parse(window.localStorage.getItem('tags') || '[]')
    if (tags_.length === 0) {
      tags_ = [
        {id: generateId(), name: '衣'},
        {id: generateId(), name: '食'},
        {id: generateId(), name: '住'},
        {id: generateId(), name: '行'}
      ]
    }
    setTags(tags_)
  }, [])

  useUpdate(() => {
    window.localStorage.setItem('tags', JSON.stringify(tags))
  }, tags)

  const findTag = (id: number) => {
    return tags.filter(t => t.id === id)[0]
  }

  const findTagIndex = (id: number) => {
    let result = -1
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].id === id) {
        result = i
        break
      }
    }
    return result
  }

  const getTagName = (id: number) => {
    const tag = tags.filter(t => t.id === id)[0]
    return tag ? tag.name : ''
  }

  const addTag = () => {
    const name = window.prompt('请输入标签名称')
    if (name !== null && name !== '') {
      const id = generateId()
      setTags([...tags, {id, name}])
    }
  }

  const updateTag = (id: number, {name}: {name: string}) => {
    setTags(tags.map(t => t.id === id ? {id, name} : t))
  }

  const removeTag = (id: number) => {
    setTags(tags.filter((tag) => tag.id !== id))
  }

  return {tags, setTags, findTag, findTagIndex, getTagName, addTag, updateTag, removeTag}
}

export {useTags}
